import React from 'react';
import { Card, Nav, Button } from 'bootstrap-4-react'
import PersonIcon from '@mui/icons-material/Person'
import { ListGroup } from 'bootstrap-4-react'
import img_src from '../../assets/image/fitness.jpg'

const IAPViewer = () => {
    return (
        <React.Fragment>
            <Card text="center">
                <Card.Header>
                    <Nav cardHeaderPills>
                        <Nav.ItemLink active href="#"><PersonIcon />Treinee</Nav.ItemLink>
                        <Nav.ItemLink disabled href="#">TreineeID</Nav.ItemLink>
                    </Nav>
                    <strong style={{float: 'rigth'}}>ActivityProvider</strong>
                </Card.Header>
                <Card.Body>
                    <Card style={{ width: '18rem' }}>
                        <Card.Image src={img_src} top />
                        <Card.Body>
                            <Card.Title>Plano de treino</Card.Title>
                            <Card.Text>
                                <strong>Coach name:</strong> <br></br>
                                <strong>CoachID:</strong>
                            </Card.Text>
                        </Card.Body>
                        <ListGroup flush>
                            <ListGroup.Item>N de sesses por semana: <br></br><strong style={{color: 'coral'}}>(3)</strong><span style={{fontSize: '12px'}}>Dias</span></ListGroup.Item>
                            <ListGroup.Item>Media de durao nas sesses de treino: <br></br><strong style={{color: 'coral'}}>(30)</strong><span style={{fontSize: '12px'}}>Hrs/Min</span></ListGroup.Item>
                            <ListGroup.Item>Prazo do plano de actividade: <br></br> <strong style={{color: 'coral'}}>(1 Semana)</strong></ListGroup.Item>
                        </ListGroup>
                        <Card.Body>
                        <Button info outline>Relatrio das sesses (Analytics)</Button>
                        </Card.Body>
                    </Card>
                </Card.Body>
            </Card>
        </React.Fragment>
    )
}


export default IAPViewer