import { Route, Routes } from "react-router-dom"
import './assets/css/App.css'
import { useEffect, useState } from "react"
import Main from "./pages/Main"
import Loger from "./pages/Loger"
import ErrorPage from "./pages/ErrorPage"
import ConfigIAP from "./components/Invenira/ConfigIAP"
import IAPViewer from "./components/ActivityProvider/IAPViewer"


const App = () => {
  const [loading, setLoading] = useState(true)
  useEffect(() => {
    setTimeout(() => {
      setLoading(false)
    }, 1000)
  },[])


  return (
    <div className="container">
      <Routes>
        <Route path='/' element={loading ? <Loger /> : <Main/>} />
        <Route path='/page/configActivity' element={loading ? <Loger /> : <ConfigIAP/>} />
        <Route path='/page/activityProvider' element={loading ? <Loger /> : <IAPViewer/>} />
        <Route path='*' element={<ErrorPage />} />
      </Routes>
    </div>
  )
}

export default App
