import { Divider } from "@material-ui/core"
import Header from "../Header"
import PropTypes from 'prop-types';
import { useAutocomplete } from '@mui/base/useAutocomplete';
import CheckIcon from '@mui/icons-material/Check';
import CloseIcon from '@mui/icons-material/Close';
import { styled } from '@mui/material/styles';
import { autocompleteClasses } from '@mui/material/Autocomplete';
import { useForm } from "react-hooks-helper"
import InputAdornment from '@mui/material/InputAdornment'
import AccountCircleIcon from '@mui/icons-material/AccountCircle'
import InputLabel from '@mui/material/InputLabel'
import MenuItem from '@mui/material/MenuItem'
import FormControl from '@mui/material/FormControl'
import FormHelperText from '@mui/material/FormHelperText'
import axios from "axios"
import Select from '@mui/material/Select'
import { useEffect } from "react";
import { TextField } from "@mui/material";
import { Button as BaseButton } from '@mui/base/Button';

/*
    exercises: {
            value: [],
        }
         sessions_per_week: '',
        sessions_duration: '',
        IAP_periodicity: ''
        */
const Root = styled('div')(
    ({ theme }) => `
            color: ${theme.palette.mode === 'dark' ? 'rgba(255,255,255,0.65)' : 'rgba(0,0,0,.85)'
        };
            font-size: 14px;
          `,
);

const Label = styled('label')`
            padding: 0 0 4px;
            line-height: 1.5;
            display: block;
          `;

const InputWrapper = styled('div')(
    ({ theme }) => `
            width: 300px;
            border: 1px solid ${theme.palette.mode === 'dark' ? '#434343' : '#d9d9d9'};
            background-color: ${theme.palette.mode === 'dark' ? '#141414' : '#fff'};
            border-radius: 4px;
            padding: 1px;
            display: flex;
            flex-wrap: wrap;
          
            &:hover {
              border-color: ${theme.palette.mode === 'dark' ? '#177ddc' : '#40a9ff'};
            }
          
            &.focused {
              border-color: ${theme.palette.mode === 'dark' ? '#177ddc' : '#40a9ff'};
              box-shadow: 0 0 0 2px rgba(24, 144, 255, 0.2);
            }
          
            & input {
              background-color: ${theme.palette.mode === 'dark' ? '#141414' : '#fff'};
              color: ${theme.palette.mode === 'dark' ? 'rgba(255,255,255,0.65)' : 'rgba(0,0,0,.85)'
        };
              height: 30px;
              box-sizing: border-box;
              padding: 4px 6px;
              width: 0;
              min-width: 30px;
              flex-grow: 1;
              border: 0;
              margin: 0;
              outline: 0;
            }
          `,
);

function Tag(props) {
    const { label, onDelete, ...other } = props;
    return (
        <div {...other}>
            <span>{label}</span>
            <CloseIcon onClick={onDelete} />
        </div>
    );
}

Tag.propTypes = {
    label: PropTypes.string.isRequired,
    onDelete: PropTypes.func.isRequired,
};

const StyledTag = styled(Tag)(
    ({ theme }) => `
            display: flex;
            align-items: center;
            height: 24px;
            margin: 2px;
            line-height: 22px;
            background-color: ${theme.palette.mode === 'dark' ? 'rgba(255,255,255,0.08)' : '#fafafa'
        };
            border: 1px solid ${theme.palette.mode === 'dark' ? '#303030' : '#e8e8e8'};
            border-radius: 2px;
            box-sizing: content-box;
            padding: 0 4px 0 10px;
            outline: 0;
            overflow: hidden;
          
            &:focus {
              border-color: ${theme.palette.mode === 'dark' ? '#177ddc' : '#40a9ff'};
              background-color: ${theme.palette.mode === 'dark' ? '#003b57' : '#e6f7ff'};
            }
          
            & span {
              overflow: hidden;
              white-space: nowrap;
              text-overflow: ellipsis;
            }
          
            & svg {
              font-size: 12px;
              cursor: pointer;
              padding: 4px;
            }
          `,
);

const Listbox = styled('ul')(
    ({ theme }) => `
            width: 300px;
            margin: 2px 0 0;
            padding: 0;
            position: absolute;
            list-style: none;
            background-color: ${theme.palette.mode === 'dark' ? '#141414' : '#fff'};
            overflow: auto;
            max-height: 250px;
            border-radius: 4px;
            box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
            z-index: 1;
          
            & li {
              padding: 5px 12px;
              display: flex;
          
              & span {
                flex-grow: 1;
              }
          
              & svg {
                color: transparent;
              }
            }
          
            & li[aria-selected='true'] {
              background-color: ${theme.palette.mode === 'dark' ? '#2b2b2b' : '#fafafa'};
              font-weight: 600;
          
              & svg {
                color: #1890ff;
              }
            }
          
            & li.${autocompleteClasses.focused} {
              background-color: ${theme.palette.mode === 'dark' ? '#003b57' : '#e6f7ff'};
              cursor: pointer;
          
              & svg {
                color: currentColor;
              }
            }
          `,
);

const ConfigIAP = () => {
    const [formData, setForm] = useForm({
        treineeID: '',
        sessions_per_week: '',
        sessions_duration: '',
        IAP_periodicity: ''
    })

    const {
        treineeID,
        sessions_per_week,
        sessions_duration,
        IAP_periodicity
    } = formData

    const {
        getRootProps,
        getInputLabelProps,
        getInputProps,
        getTagProps,
        getListboxProps,
        getOptionProps,
        groupedOptions,
        value,
        focused,
        setAnchorEl,
    } = useAutocomplete({
        id: 'customized-hook-demo',
        defaultValue: [exercisesValues[1]],
        multiple: true,
        options: exercisesValues,
        isOptionEqualToValue: (option, value) => option.title === value.title,
        getOptionLabel: (option) => option.title,
    })

    useEffect(() => {
        console.log('select box---------')
    }, [])


    return (
        <div>
            <Header />
            <Divider style={{ marginBottom: '10px' }} />
            <h4>Criar/configurar IAP-Inventive Activities Plan</h4>
            <hr></hr>
            <TextField
                label="Treinee"
                name="treineeID"
                style={{ marginBottom: '10px', width: '240px' }}
                size="small"
                value={treineeID}
                onChange={setForm}
                placeholder='ID'
                InputProps={{
                    startAdornment: <InputAdornment position="start" style={{ marginTop: '0px' }}><AccountCircleIcon /></InputAdornment>
                }}
            />

            <Root>
                <div {...getRootProps()}>
                    <Label {...getInputLabelProps()}>Exerccios a serem realizados</Label>
                    <InputWrapper ref={setAnchorEl} className={focused ? 'focused' : ''}>
                        {value.map((option, index) => (
                            <StyledTag label={option.title} {...getTagProps({ index })} />
                        ))}
                        <input {...getInputProps()} />
                    </InputWrapper>
                </div>
                {groupedOptions.length > 0 ? (
                    <Listbox {...getListboxProps()}>
                        {groupedOptions.map((option, index) => (
                            <li {...getOptionProps({ option, index })}>
                                <span>{option.title}</span>
                                <CheckIcon fontSize="small" />
                            </li>
                        ))}
                    </Listbox>
                ) : null}
            </Root>

            <TextField
                label="#"
                name="sessions_per_week"
                style={{ marginTop: '10px', marginRight: '10px', width: '120px' }}
                size="small"
                value={sessions_per_week}
                onChange={setForm}
                helperText="Numero de sesses de treino por semana"
                InputProps={{
                    endAdornment: <InputAdornment position="end">Dias</InputAdornment>,
                }}
            />

            <TextField
                label="#"
                name="sessions_duration"
                style={{ marginTop: '10px', marginRight: '10px', width: '170px' }}
                size="small"
                value={sessions_duration}
                onChange={setForm}
                helperText="Media de durao das sesses de treino"
                InputProps={{
                    endAdornment: <InputAdornment position="end">Hrs/minutos</InputAdornment>,
                }}
            />

            <FormControl sx={{ minWidth: 250, marginTop: '10px'}} size="small">
                <InputLabel id="demo-select-small-label">Periodo</InputLabel>
                <Select
                    labelId="demo-select-small-label"
                    name="IAP_periodicity"
                    value={IAP_periodicity}
                    id="demo-select-small"
                    label="Perodo"
                    onChange={setForm}
                >
                    <MenuItem value={'1Week'}>1 Semana</MenuItem>
                    <MenuItem value={'2Weeks'}>2 Semana</MenuItem>
                    <MenuItem value={'1Month'}>1 Ms</MenuItem>
                    <MenuItem value={'2Months'}>2 Meses</MenuItem>
                    <MenuItem value={'3Months'}>3 Meses</MenuItem>
                </Select>
                <FormHelperText>Prazo do IAP</FormHelperText>
            </FormControl>
            <br></br>
            <hr></hr>
            <Button
                onClick={() => {
                    const data = {
                        IAP_ID: '001',
                        coachID: '872610106',
                        treineeID: '050425648',
                        exercises: {
                            value: value
                        },
                        sessions_per_week: formData.sessions_per_week,
                        sessions_duration: formData.sessions_duration,
                        IAP_periodicity: formData.IAP_periodicity
                    }
               
                   
                    axios.post('/api/configActivity', data).then(result => {
                        console.log(result)
                    }).catch(e => {
                        console.log(e);
                    })
                }}
            >Criar IAP</Button>
        </div>
    )
}

 

const exercisesValues = [
    { title: 'Aerobica' },
    { title: 'Agachamento' },
    { title: 'Salto a corda' },
    { title: 'Levantamento de peso' },
    { title: 'Flexes' },
    { title: 'Natao' },
    { title: 'Yoga' },
    { title: 'Dana'},
    { title: 'Desportivo' }
]

const blue = {
    200: '#99CCFF',
    300: '#66B2FF',
    400: '#3399FF',
    500: '#007FFF',
    600: '#0072E5',
    700: '#0066CC',
};

const Button = styled(BaseButton)(
    ({ theme }) => `
    font-family: 'IBM Plex Sans', sans-serif;
    font-weight: 600;
    font-size: 0.875rem;
    line-height: 1.5;
    background-color: ${blue[500]};
    padding: 8px 16px;
    border-radius: 8px;
    margin-top: '10px';
    color: white;
    transition: all 150ms ease;
    cursor: pointer;
    border: 1px solid ${blue[500]};
    box-shadow: 0 2px 1px ${theme.palette.mode === 'dark' ? 'rgba(0, 0, 0, 0.5)' : 'rgba(45, 45, 60, 0.2)'
        }, inset 0 1.5px 1px ${blue[400]}, inset 0 -2px 1px ${blue[600]};
  
    &:hover {
      background-color: ${blue[600]};
    }
  
    &:active {
      background-color: ${blue[700]};
      box-shadow: none;
      transform: scale(0.99);
    }
  
    &:focus-visible {
      box-shadow: 0 0 0 4px ${theme.palette.mode === 'dark' ? blue[300] : blue[200]};
      outline: none;
    }
  `,
);

export default ConfigIAP

