import { Divider } from "@material-ui/core"
import axios from "axios"
import { useEffect, useState } from "react"
import { Alert } from 'bootstrap-4-react';
import Header from "../components/Header";



const Main = () => {
    const [regeditJSON, setJSON] = useState({})
    useEffect(() => {
        axios.get('/activityProvider').then(result => {
            console.log(result.data)
            setJSON(result.data)
        })
    }, [])

    return (
        <div>
            <Header />
            <Divider style={{ marginBottom: '10px' }} />
            <h4>Inventive Activities Plan JSON file</h4>
            <hr></hr>
            <div>
                <p style={{fontSize: '13px', color: 'coral'}}>{regeditJSON.name}</p>
                <Alert secondary>{regeditJSON.config_url}
                    <button type="button" class="btn btn-info"
                        style={{ float: 'right', marginTop: '-7px', marginRight: '-15px' }}
                        onClick={() => {
                            var parser = document.createElement('a');
                            parser.href = regeditJSON.config_url
                            window.open(parser.pathname)
                        }}
                    >OPEN</button>
                </Alert>
                <Alert secondary>{regeditJSON.json_params_url}
                    <button type="button" class="btn btn-info"
                        style={{ float: 'right', marginTop: '-7px', marginRight: '-15px' }}
                        onClick={() => {
                            window.open(regeditJSON.json_params_url)
                        }}
                    >OPEN</button>
                </Alert>
                
                <Alert success>{regeditJSON.user_url}</Alert>
                <Alert success>{regeditJSON.analytics_url}</Alert>

                
                <Alert secondary>{regeditJSON.analytics_list_url}
                    <button type="button" class="btn btn-info"
                        style={{ float: 'right', marginTop: '-7px', marginRight: '-15px' }}
                        onClick={() => {
                            window.open(regeditJSON.analytics_list_url)
                        }}
                    >OPEN</button>
                </Alert>

            </div>
        </div>
    )
}
export default Main